export function uploadMoreFile(data) {
  let newObj = {};
  if (data.table == undefined) {
    newObj = { company_id: '111', type: 1, floor: 1, current: 0 }
  } else {
    newObj = { company_id: '111', table: '111', type: 1, floor: 1, current: 0 }
  }
  let signatureObj = addSignature(newObj);
  // console.log(signature);
  let formData = new FormData();
  formData.append('signature', signatureObj.signature)
  for (let key in data) {
    if (data[key] instanceof Array) {
      let files = data[key];
      for (let file in files) {
        formData.append(key + '[]', files[file]);
      }
    } else {
      formData.append(key, data[key])
    }
  };
  // for(let file in files){
  //   formData.append('',files[file]);
  // }
  return Vue.prototype.$axios({
    //api.Hallowmas url.js
    url: api.url + '/web/Company/uploadMoreFile',
    method: 'post', //
    data: formData,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}
